<template>
  <!--   表单搜索   -->
  <div class="form">
    <el-select
      v-model="paramsData.orderState"
      placeholder="请选择订单状态"
      clearable
      @change="getList"
    >
      <el-option
        v-for="item in orderStatusList"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      />
    </el-select>
    <el-input
      v-model="paramsData.orderNo"
      placeholder="订单编号"
      clearable
      @blur="getList"
      @clear="getList"
      @keyup.enter="getList"
    ></el-input>
    <el-date-picker
      v-model="dateList"
      @change="changeDate"
      type="daterange"
      value-format="YYYY-MM-DD"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
    >
    </el-date-picker>
  </div>
  <el-table :data="pageData.list" class="custom-table">
    <el-table-column
      prop="buyTime"
      label="下单时间"
      align="center"
      min-width="150"
    ></el-table-column>
    <el-table-column
      prop="appName"
      label="产品名称"
      align="center"
    ></el-table-column>
    <el-table-column
      prop="orderNo"
      label="订单编号"
      align="center"
      min-width="180"
    ></el-table-column>
    <el-table-column prop="cycle" label="周期" align="center"></el-table-column>
    <el-table-column
      prop="realPrice"
      label="实付金额"
      align="center"
    ></el-table-column>
    <el-table-column
      prop="amount"
      label="返佣金额"
      align="center"
    ></el-table-column>
    <el-table-column
      prop="orderStatusName"
      label="状态"
      align="center"
      width="150"
      fixed="right"
    >
      <template #default="{ row }">
        <el-tag :type="row.orderStateStyleType">{{
          row.orderStateName
        }}</el-tag>
      </template>
    </el-table-column>
    <template #empty>
      <TableEmpty empty-text="暂无交易订单"></TableEmpty>
    </template>
  </el-table>
  <!--   分页   -->
  <PagePagination
    :pageTotal="pageData.pageTotal"
    :pageSize="pageData.pageSize"
    :pageNumber="pageData.pageNumber"
    @emitPageSizeChange="pageSizeChange"
    @emitPageNumberChange="pageNumberChange"
  ></PagePagination>
</template>

<script lang="ts" setup>
import { ref, shallowReactive } from "vue";

import { orderStatusList } from "@assets/typeStatus";
import { usePage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";

import { customerCommissionApi } from "@api/customer-management-api";

const { routeQuery } = useVueRouter();

const dateList = ref([]);
const paramsData = shallowReactive({
  endTime: "",
  startTime: "",
  orderNo: "",
  orderState: "",
  customerId: routeQuery.value.profileId,
});
const { getList, pageNumberChange, pageSizeChange, pageData } = usePage({
  listApi: customerCommissionApi,
  props: {
    pageNumber: "current",
    pageSize: "size",
  },
  searchParams: () => {
    return {
      ...paramsData,
    };
  },
});

// 选择日期
const changeDate = (value) => {
  if (value) {
    paramsData.startTime = value[0];
    paramsData.endTime = value[1];
  } else {
    paramsData.startTime = "";
    paramsData.endTime = "";
    dateList.value = [];
  }
  getList();
};
</script>
<style lang="scss" scoped>
.rebate-container {
  flex-grow: 1;
  margin-left: 20px;
  margin-bottom: 0;
}
.form {
  position: absolute;
  top: 30px;
  right: 22px;
  display: inline-flex;
  justify-content: flex-end;
  > div {
    margin-right: 10px;
  }
  .el-select,
  .el-input {
    flex: 0 0 200px;
  }
  :deep(.el-date-editor) {
    flex: 0 0 280px;
  }
}
</style>
